import React, { useState, useEffect } from "react";
import BlazeInstance from '../abi/Blaze.json';
import GanjaInstance from '../abi/Ganja.json'
import Web3 from 'web3';
import Green from '../green.png';
import BlazeLogo from '../Blaze.png';
import GanjaLogo from '../Ganja.png';
import { blazeabi, ganjaabi } from '../abi/abi';
import { web3Instance } from "../web3";
const numToLocale = (number, decimals) =>
  Number(number / 10 ** decimals).toFixed(2);
const numToLocale2 = (number, decimals) =>
    Number(number / 10 ** decimals).toFixed(0);
    const web3 = new Web3(Web3.givenProvider);
    const contractAddr = '0xD581A700d6dF06Bc7f755816f9d9317d14a13f7c';
    const ganjaAddr = '0x447c324f7eb99e512ff18308203b36d3c2607e0e';
    const Blaze = new web3.eth.Contract(blazeabi, contractAddr);
    const Ganja = new web3.eth.Contract(ganjaabi, ganjaAddr);

function Home() {


    let [totalSupply, setTotalSupply] = useState("---");
    let [tax, setTax] = useState("---");
    let [login, setLogin] = useState(false);
    let [burned, setBurned] = useState("---");
    let [balanceContract, setBalanceContract] = useState("---");
    let [lockFee, setLockFee] = useState("---");
    let [lastSmoke, setLastSmoke] = ("---");
    let [blazeLP, setBlazeLP] = ("---");

    //ganja variables

    let [ganjaSupply, setGanjaSupply] = useState("---");
    let [ganjaBurned, setGanjaBurned] = useState("---");
    let [ganjaLP, setGanjaLP] = useState("---");
    let [ganjaPercent, setGanjaPercent] = useState("---");
    let [ganjaTrue, setGanjaTrue] = useState("---");
    let [ganjaCommunity, setGanjaCommunity] = useState("---");




    useEffect(() => {
       initiateWeb3();
     }, []);
     const initiateWeb3 = async () => {
       const Instance = await web3Instance();
             let contractInstance = new Instance.eth.Contract(blazeabi, "0xD581A700d6dF06Bc7f755816f9d9317d14a13f7c"
           );
             let contractInstance2 = new Instance.eth.Contract(ganjaabi, "0x447c324f7eb99e512ff18308203b36d3c2607e0e"
         );




      setTax(numToLocale(await contractInstance.methods._taxFee().call(), 2)
    );
      setLockFee(numToLocale(await contractInstance.methods._lockFee().call(), 2)
    );
      setTotalSupply(numToLocale(await contractInstance.methods.totalSupply().call(), 9)
    );
      setBalanceContract(numToLocale(await contractInstance.methods.balanceOf(contractAddr).call(), 9)
    );
      setBurned(numToLocale(await contractInstance.methods._tBurnTotal().call(), 9)
    );


    setGanjaSupply(numToLocale(await contractInstance2.methods.totalSupply().call(), 18)
  );
    setGanjaBurned(numToLocale(await contractInstance2.methods.balanceOf("0x000000000000000000000000000000000000dead").call(), 18)
  );
    setGanjaLP(numToLocale(await contractInstance2.methods.balanceOf("0xc69E8D772485867d2BFdFA1847F2Ea9acdA77633").call(), 18)
  );
    setGanjaPercent(numToLocale(await contractInstance2.methods.basePercent().call(), 2)
  );
  setGanjaCommunity(numToLocale(await contractInstance2.methods.balanceOf("0x98B58134671b9219B461dD02191585F65753972e").call(), 18)
);
  setGanjaTrue(numToLocale((ganjaSupply - ganjaBurned), 0)
);




  };

  //Button Functions

  const approve = async (e) => {
    const accounts = await window.ethereum.enable();
    const account = accounts[0];
    const gas = 1000000;
    const amount = 10000000000000;
    const result = await Blaze.methods.approve(contractAddr, amount).send({
      from: account,
      gas
    })

    console.log(result);
  }


    const burnPoolTokens = async (e) => {
      const accounts = await window.ethereum.enable();
      const account = accounts[0];
      const gas = 1000000;
      const result = await Blaze.methods.smoke().call({
        from: account,
        gas
      })
      console.log(result);
    }







  return (

<>
    <div class='title'>

    <h1>The Future of Finance is Green</h1>
    </div>

    <div class='row'>



    <div class="column">


    <div className="numbers, box">
    <img src={GanjaLogo} className="logo" alt-text="$GANJA"/>

      <h2>The first 100% community-driven deflationary token on Binance Smart Chain</h2>
      <br/>
      <h2>Ganja Supply : { ganjaSupply }</h2>
      <h2>Ganja Burned : { ganjaBurned }</h2>
      <h2>Ganja LP Balance : { ganjaLP }</h2>
      <h2>Community Fund: { ganjaCommunity }</h2>
    
      <br/>

    </div>

    </div>


    <div class="column">


    <div className="numbers, box">
    <img src={BlazeLogo} className="logo" alt-text="$GANJA"/>
    <h2>Built to reward holders while supporting the trees.finance $GANJA ecosystem</h2>
    <br/>


      <h2>Blaze Supply : { totalSupply }</h2>
      <h2>Blaze Burned : { burned }</h2>
      <h2>Rebalancer : { balanceContract }</h2>
      <h2>Next Smoke Sesh:{ lastSmoke }</h2>

    </div>

    </div>


    </div>

    <div class='burn'>
    <button className="glow2" onClick={ burnPoolTokens }type="button">🔥SMOKE UP🔥</button>
    </div>




</>

  );
  }

export default Home;
